body {
  background-color: rgb(234, 234, 234);
}

.masthead {
  display: flex;
  align-items: center;
  background-color: rgb(245, 245, 245) !important;
  height: 100vh;
}

.masthead .ui.menu .ui.button,
.ui.menu a.ui.inverted.button {
  margin-left: 0.5em;
}

.masthead h1.ui.header {
  font-size: 4em;
  font-weight: normal;
}

.masthead h2 {
  font-size: 1.7em;
  font-weight: normal;
}

.react-datepicker-wrapper {
  width: 100%;
}
